import React from 'react';
import 'tachyons';
import './App.css';
import Home from './components/home/Home';
import Navigation from './components/navigation/Navigation';
import Shop from './components/shop/Shop'
import ShoppingCart from './components/shippingCart/ShoppingCart';
import CheckOut from './components/checkout/CheckOut';
import Submit from './components/checkout/Submit'




class app extends React.Component{

  constructor(){
    super()
    this.state={
      route: 'home',
      cartList:[],
      cartTotal:0,
      onSearchField:''
      
    }

  } 

  onSearchChange=(event)=>{
    this.setState({onSearchField:event.target.value})
  }

  plusItem=(i) =>{
    let items = this.state.cartList
    let total = 0
    
    items[i].qty++
    
    
    this.setState({cartList:items})
    items.forEach((item,i)=>{
      total += (items[i].price*items[i].qty)
    })
    this.setState({cartTotal:total})

  }

  lessItem=(i) =>{
    let items = this.state.cartList
    let total = 0
    
    if(items[i].qty>1){
      items[i].qty--
    }else{delete items[i];}
    
    
    this.setState({cartList:items})
    items.forEach((item,i)=>{
      total +=  (items[i].price*items[i].qty)
    })
    this.setState({cartTotal:total})

  }

  onAddCartList =(obj) =>{
  
    let items = this.state.cartList
    let total = 0
    let add = obj;

    
      items.push(obj)
  
    if(items.length>1){ 
  
      for(let i=0; i<items.length-1;i++){
      if(items[i].id === add.id){
        items[i].qty++
        items.pop();
        break
      }
    }}
   

   

    
    items.forEach((item,i)=>{
      total += (items[i].price * items[i].qty)
    })
    this.setState({cartTotal:total})

  }

  

  onRouteChange =(route) =>{
    this.setState({route: route})
    
  }



  render(){
    return(
      
      <div classname='app'>

        <Navigation onRouteChange={this.onRouteChange}/>
        
        {
          this.state.route==='home'
          ?<div><Home/></div>
          :(
            this.state.route==='shop'
            ?<div><Shop onAddCartList={this.onAddCartList} onSearchField={this.state.onSearchField} onSearchChange={this.onSearchChange}/></div>
            :(this.state.route==='cart'
              ?<div><ShoppingCart cartList={this.state.cartList} cartTotal={this.state.cartTotal} plusItem={this.plusItem} lessItem={this.lessItem} onRouteChange={this.onRouteChange}/></div>
              :(this.state.route === 'checkout'
                ?<div><CheckOut onRouteChange={this.onRouteChange}/></div>
                :(<div><Submit/></div>

                ))))
              
           
          
            

        }
                     
      </div>


    )

  }
  
}

export default app;