import React from 'react';
import icon from './credit-card-regular.svg'
const CheckOut =({onRouteChange})=>{

    return(
        <div>
            <div className='measure center'>
                <h1>Delivery Details</h1>
                <label for="name" className="f6 b db mb2">Name</label>
                <input id="name" className="input-reset ba b--black-20 pa2 mb2 db w-100" type="text" aria-describedby="name-desc"/>
                <label for="email" className="f6 b db mb2">Email</label>
                <input id="email" className="input-reset ba b--black-20 pa2 mb2 db w-100" type="email" aria-describedby="name-desc"/>
                <label for="post" className="f6 b db mb2">Post Code</label>
                <input id="post" className="input-reset ba b--black-20 pa2 mb2 db w-100" type="text" aria-describedby="name-desc"/>
                <label for="city" className="f6 b db mb2">City</label>
                <input id="city" className="input-reset ba b--black-20 pa2 mb2 db w-100" type="text" aria-describedby="name-desc"/>
                <label for="address" className="f6 b db mb2">Address</label>
                <input id="address" className="input-reset ba b--black-20 pa2 mb2 db w-100" type="text" aria-describedby="name-desc"/>
                <legend className="fw7 mb2">Payment Options</legend>
                <div id='uncheck'>
                <input className="mr2" type="radio" id='cod' name='po' value='cod'/>
                <label for="cod" className="lh-copy">Cash On Delivery</label><br></br>
                <input className="mr2 " type="radio" id='op' name='po' value='op'/>
                <label for="op" className="lh-copy">Online Payment </label><br></br>
                <img  className='w-10 mt1' src={icon}/>             
                </div>
            </div>
            <div style={{textAlignLast:'end'}}>
                <p  onClick={()=>onRouteChange('cart')} className="mt4 f6 link dim ba bw1 ph3 pv3 mb2 dib near-black pointer" style={{margin:'1em'}}>Previous</p>
                <p  onClick={()=>onRouteChange('submit')} className="mt4 f6 link dim ba bw1 ph3 pv3 mb2 dib near-black pointer" style={{margin:'1em'}}>Submit</p>
            </div>
        </div>
    )

}

export default CheckOut;