import React from 'react';
import plus from './plus-solid.svg';
import minus from './minus-solid.svg'



const ShoppingCart = ({cartList, cartTotal, plusItem, lessItem, onRouteChange})=>{


    return(
        
        <div class="pa10">
        <div class="overflow-auto">
          <table class="f6 w-100 mw8 center" cellspacing="0">
            <thead>
              <tr class="stripe-dark">
                <th class="fw6 tl pa3 bg-white">Products</th>
                <th scope='col' class="fw6 tl pa3 bg-white">Qty</th>
                <th scope='col' class="fw6 tl pa3 bg-white">Price</th>
                <th scope='col' class="fw6 tl pa3 bg-white">Sub Total</th>
                
              </tr>
            </thead>
            <tbody class="lh-copy">

            {
                cartList.map((item,i)=>{
                    
                    return(
                        
                        <tr class="striped--light-gray">
                            <td  class="pa3">{cartList[i].name}</td>
                            <td  class="pa3">{cartList[i].qty}</td>
                            <td  class="pa3">{`₱ ${cartList[i].price}`}</td>
                            <td  class="pa3">{`₱ ${cartList[i].price * cartList[i].qty}`}</td>
                            <td>
                              <img  onClick={()=>plusItem(i)} className="dib grow  pointer ph1-l" src={plus} style={{width:'15px'}}/>&nbsp;
                              <img  onClick={()=>lessItem(i)} className="dib grow  pointer ph1-l" src={minus} style={{width:'15px'}}/>
                            </td>
                        </tr>

                    )
                })
            }

            </tbody>
            <tfoot>
                <tr>
                    <th className= 'fw8 tl pa2 bg-black-20'  ></th>
                    <th className= 'fw8 tl pa2 bg-black-20'  >TOTAL</th>
                    <th className= 'fw8 tl pa2 bg-black-20' ></th>
                    <th className='tl pa2 bg-black-20'>{` ₱ ${cartTotal}`}</th>
                    <th className= 'fw8 tl pa2 bg-black-20' ></th>
                </tr>
            </tfoot>
          </table>
        </div>
          <div style={{textAlignLast:'end'}}>
            <p  onClick={()=>onRouteChange('checkout')} className="mt4 f6 link dim ba bw1 ph3 pv3 mb2 dib near-black pointer" style={{margin:'1em'}}>Checkout</p>
          </div>
      </div>
        
    )
}
        
    


export default ShoppingCart;