import React from 'react';
import products from'./products' ;

const Shop = ({onAddCartList, onSearchField, onSearchChange}) =>{
    
    const filtered = products.filter((product , i) =>{
        return product.name.toLowerCase().trim().includes(onSearchField.trim().toLowerCase());
      })
    
    return(
    
    <div>

        <div className='pa4 flex '>
            <input
                className='pa3 ba'
                type='search'
                placeholder='search'
                onChange={onSearchChange}
            />
        </div>
        
        

        <div className='flex flex-wrap outline'>
        
        
        
            {onSearchField.length === 0 
                ?products.map((p,i)=>{
                    return(
                        card(i, onAddCartList, p)
                        
                    )
                }):(
                    
                     filtered.map((p,i)=>{
                         
                    return filteredCard(filtered, i, onAddCartList, p)
                     })
                    
                    )

            }
        </div>
    </div>
    )
    

}

export default Shop;

function filteredCard(filtered, i, onAddCartList, p) {
    return (<article className="br2 ba dark-gray b--black-10 mv4 w-100  w-50-m w-25-l mw5 center shadow-1 grow">
        <img src={filtered[i].img} className="db w-100 h-auto br2 br--top" alt="product" />
        <div className="pa2 ph3-ns pb3-ns">
            <div className="dt w-100 mt1">
                <div className="dtc">
                    <h1 className="f5 f4-ns mv0">{filtered[i].name}</h1>
                </div>
                <div className="dtc tr">
                    <h2 className="f5 mv0">{`₱ ${filtered[i].price}`}</h2>
                </div>
            </div>
            <p onClick={() => onAddCartList(
                {   id:p.id,
                    qty:p.qty,
                    name: p.name,
                    price: p.price
                })} class="link tc ph3 pv1 db bg-animate bg-dark-green hover-bg-light-green white f6 br1 pointer">Add To Cart</p>
        </div>
    </article>);
}

function card(i, onAddCartList, p) {
    return <article className="br2 ba dark-gray b--black-10 mv4 w-100  w-50-m w-25-l mw5 center shadow-1 grow">
        <img src={products[i].img} className="db w-100 h-auto br2 br--top" alt="product" />
        <div className="pa2 ph3-ns pb3-ns">
            <div className="dt w-100 mt1">
                <div className="dtc">
                    <h1 className="f5 f4-ns mv0">{products[i].name}</h1>
                </div>
                <div className="dtc tr">
                    <h2 className="f5 mv0">{`₱ ${products[i].price}`}</h2>
                </div>

            </div>
            <p onClick={() => onAddCartList(
                {   id:p.id,
                    qty:p.qty,
                    name: p.name,
                    price: p.price
                })} class="link tc ph3 pv1 db bg-animate bg-dark-green hover-bg-light-green white f6 br1 pointer">Add To Cart</p>
        </div>
    </article>;
}
